import React from "react"
import ProjectLink from "../components/project-link"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { CardDeck, Card, Container } from "react-bootstrap"
import { graphql } from "gatsby"

const ProjectsPage = ({
  data: {
    allProjectsJson: { edges },
  },
}) => {
  const Projects = edges
    //.filter(edge => edge.node.name !== "alzeck.github.io")
    .map(edge => <ProjectLink project={edge.node} />)
  return (
    <Layout>
      <SEO title="Projects" />
      <Container
        className={"d-flex align-items-center justify-content-center px-0 my-4"}
        style={{ minHeight: "calc(100vh - 154px)" }}
      >
        <Card className={"glass"}>
          <Card.Body>
            <h5>Take a look at some of my Open Source projects: </h5>
            <hr className="mt-1" />
            <CardDeck>{Projects}</CardDeck>
          </Card.Body>
        </Card>
      </Container>
    </Layout>
  )
}
export default ProjectsPage

export const pageQuery = graphql`
  query {
    allProjectsJson(sort: { order: ASC, fields: [name] }) {
      edges {
        node {
          id
          name
          html_url
          description
          updated_at(formatString: "MMMM DD, YYYY")
          pushed_at(formatString: "MMMM DD, YYYY")
          created_at(formatString: "MMMM DD, YYYY")
          language
        }
      }
    }
  }
`
